<template>
  <main id="main-padding" class="main">
    <komponen-judul :title="namaPage" />
    <section class="section dashboard" v-if="dataAbsensi.length > 0">
      <div class="card">
        <div class="card-body">
          <button type="button" class="btn btn-primary mb-2" data-bs-toggle="modal" data-bs-target="#modal-tambah-absensi">+ Tambah</button>
          <komponen-tambah-absensi @get-data-absensi="getDataAbsensi" />
          <div :class="['scroll-container', defaultColorScrollbar, changeColorScrollbar]" @mousemove="handleMouseMove">
            <table class="table table-sm table-hover" style="min-width: 1000px">
              <thead class="bg-light">
                <tr class="my-4">
                  <th scope="col" :class="class_th()" :style="style_th()">No.</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Nama</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Status</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Keterangan</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Start</th>
                  <th scope="col" :class="class_th()" :style="style_th()">End</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Shift</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Lokasi</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Pengabsen</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Tanggal</th>
                  <th scope="col" :class="class_th()" :style="style_th()">Aksi</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in dataPagination" :key="item.id">
                  <td :style="style_td(index + 1, item.hadir)" :class="class_td()">
                    {{ index + 1 }}
                  </td>
                  <td :style="style_td(index + 1, item.hadir)" :class="class_td()">
                    {{ item.karyawan.nama }}
                  </td>
                  <td :style="style_td(index + 1, item.hadir)" :class="class_td()">
                    {{ item.hadir }}
                  </td>
                  <td :style="style_td(index + 1, item.hadir)" :class="class_td()">
                    {{ item.keterangan }}
                  </td>
                  <td :style="style_td(index + 1, item.hadir)" :class="class_td()">
                    {{ item.time_start }}
                  </td>
                  <td :style="style_td(index + 1, item.hadir)" :class="class_td()">
                    {{ item.time_end ? item.time_end : '-' }}
                  </td>
                  <td :style="style_td(index + 1, item.hadir)" :class="class_td()">
                    {{ item.shift }}
                  </td>
                  <td :style="style_td(index + 1, item.hadir)" :class="class_td()">
                    {{ item.nama_lokasi }}
                  </td>
                  <td :style="style_td(index + 1, item.hadir)" :class="class_td()">
                    {{ item.penjab }}
                  </td>
                  <td :style="style_td(index + 1, item.hadir)" :class="class_td()">
                    {{ item.tanggal }}
                    {{ convertLokal(item.tanggal) }}
                  </td>
                  <td :class="class_td()" :style="style_td(index + 1, item.hadir)">
                    <div class="btn-group" role="group" aria-label="Basic mixed styles example">
                      <button @click="deleteAbsensiById(item.id)" type="button" class="btn btn-danger"><i class="bi bi-trash3-fill"></i></button>
                      <button @click="getDataAbsensiById(item.id)" data-bs-toggle="modal" data-bs-target="#modal-edit-absensi" type="button" class="btn btn-warning"><i class="bi bi-pencil-square"></i></button>
                    </div>
                    <komponen-edit-absensi :data-absensi="dataAbsensiById" @get-data-absensi="getDataAbsensi" />
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <komponen-pagination :data="dataAbsensi" @update:data="dataSlice" />
      </div>
    </section>
    <section v-else>
      <komponen-not-found />
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import KomponenJudul from '../components/KomponenJudul.vue';
import KomponenNotFound from '../components/KomponenNotFound.vue';
import KomponenPagination from '@/components/KomponenPagination.vue';
import KomponenTambahAbsensi from '@/components/KomponenTambahAbsensi.vue';
import KomponenEditAbsensi from '@/components/KomponenEditAbsensi.vue';
import SweetAlertHelper from '@/SweetAlertHelper/SweetAlertHelper';
import { API_KEY } from '../../env';
import mixin from '@/mixin/mixin';
import router from '@/router';

export default {
  mixins: [mixin],
  name: 'pageDataAbsensi',
  components: {
    KomponenJudul,
    KomponenNotFound,
    KomponenPagination,
    KomponenTambahAbsensi,
    KomponenEditAbsensi,
  },
  data() {
    return {
      index: 0,
      dataAbsensi: [],
      dataAbsensiById: {},
      dataPagination: [],
      namaPage: 'Data Absensi',
    };
  },
  props: {
    searchResults: {
      required: false,
    },
  },
  watch: {
    searchResults: 'getDataAbsensi',
  },
  methods: {
    dataSlice(updatedData) {
      this.dataPagination = updatedData;
    },

    convertLokal(tgl) {
      const dateObject = new Date(tgl);
      return dateObject.toLocaleDateString();
    },

    getDataAbsensi() {
      axios
        .get(`${API_KEY}absensi_get_all`, {
          params: {
            search_query: this.searchResults,
          },
        })
        .then((response) => {
          this.dataAbsensi = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    getDataAbsensiById(id) {
      axios
        .get(`${API_KEY}absensi_get_by_id/${id}`)
        .then((response) => {
          this.dataAbsensiById = response.data.data;
        })
        .catch((error) => {
          router.push({ name: 'Eror', params: { msg: error } });
        });
    },
    deleteAbsensiById(id) {
      SweetAlertHelper.confirm('Apakah yakin?', 'Data akan dihapus permanen.').then((result) => {
        if (result.isConfirmed) {
          axios
            .delete(`${API_KEY}absensi_hapus/${id}`)
            .then((response) => {
              if (response.status === 204) {
                SweetAlertHelper.success('Berhasil!', 'Data telah dihapus.');
                this.getDataAbsensi();
              }
            })
            .catch((error) => {
              router.push({ name: 'Eror', params: { msg: error } });
            });
        }
      });
    },
  },

  mounted() {
    const token = localStorage.getItem('accessToken');
    if (token) {
      this.getDataAbsensi();
      this.dataSlice();
    } else {
      router.push('/login');
    }
  },
};
</script>
