import { createRouter, createWebHistory } from 'vue-router';
import LoginView from '../views/LoginView.vue';
import MainView from '../views/MainView .vue';

// PAGE USER
import pageDashboard from '@/pages/pageDashboard.vue';
import pagePresensiMeVue from '@/pages/pagePresensiMe.vue';
import pageDataAbsensiMeVue from '@/pages/pageDataAbsensiMe.vue';
import pageDataProduksiMeVue from '@/pages/pageDataProduksiMe.vue';
import pagePengajuanIzinVue from '@/pages/pagePengajuanIzin.vue';
// KOMPONEN
import ErorView from '../components/ErorView.vue';
import permissionMiddleware from '@/middleware/permissionMiddleware';
import KomponenTambahLokasi from '@/components/KomponenTambahLokasi.vue';
import KomponenEditLokasi from '@/components/KomponenEditLokasi.vue';
// MASTER DATA
import pageKaryawanAktifVue from '../pages/pageKaryawanAktif.vue';
import pageKaryawanNonAktifVue from '../pages/pageKaryawanNonAktif.vue';
import pageKaryawanGajiVue from '@/pages/pageKaryawanGaji.vue';
import pageKaryawanLokasiVue from '@/pages/pageKaryawanLokasi.vue';
import pageDataUsersVue from '@/pages/pageDataUsers.vue';
import pageKaryawanHakAksesVue from '@/pages/pageKaryawanHakAkses.vue';
// PENCATATAN KEHADIRAN
import pageCheckInVue from '@/pages/pageCheckIn.vue';
import pageCheckOutVue from '@/pages/pageCheckOut.vue';
import pagePemulihanVue from '@/pages/pagePemulihan.vue';
import pageDataAbsensiVue from '@/pages/pageDataAbsensi.vue';
import pageDataProduksiVue from '@/pages/pageDataProduksi.vue';
// LAPORAN
import pageLapAbsensiVue from '@/pages/pageLapAbsensi.vue';
import pageLapGajiVue from '@/pages/pageLapGaji.vue';
// PENGATURAN
import pagePengaturanViewVue from '@/pages/pagePengaturanView.vue';
import pageProfilVue from '@/pages/pageProfil.vue';
import store from '@/store';

const routes = [
  {
    path: '/login',
    name: 'LoginView',
    component: LoginView,
  },
  {
    path: '/eror/:msg',
    name: 'Eror',
    component: ErorView,
  },
  {
    path: '/dashboard',
    name: 'Main',
    component: MainView,
    beforeEnter: (to, from, next) => {
      if (!store.state.dataMe) {
        store.dispatch('fetchDataMe').then(() => {
          next();
        });
      } else {
        next();
      }
    },
    children: [
      {
        path: '/',
        name: 'pageDashboard',
        component: pageDashboard,
      },
      {
        path: '/presensi-me/:randomString',
        name: 'pagePresensiMe',
        component: pagePresensiMeVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
        // beforeEnter: (to, from, next) => {
        //   const id_path = localStorage.getItem('id-path');
        //   const fullUrl = window.location.href;
        //   if (id_path === fullUrl) {
        //     next();
        //   } else {
        //     next('/halaman_error');
        //   }
        // },
      },
      {
        path: '/data-absensi-me/:randomString',
        name: 'pageDataAbsensiMe',
        component: pageDataAbsensiMeVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/data-produksi-me/:randomString',
        name: 'pageDataProduksiMe',
        component: pageDataProduksiMeVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/pengajuan-izin/:randomString',
        name: 'pagePengajuanIzin',
        component: pagePengajuanIzinVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/data-karyawan/:randomString',
        name: 'pageKaryawanAktif',
        component: pageKaryawanAktifVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/data-karyawan-exp/:randomString',
        name: 'pageKaryawanNonAktif',
        component: pageKaryawanNonAktifVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/data-karyawan-gaji/:randomString',
        name: 'pageKaryawanGaji',
        component: pageKaryawanGajiVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/data-karyawan-lokasi/:randomString',
        name: 'pageKaryawanLokasi',
        component: pageKaryawanLokasiVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/data-users/:randomString',
        name: 'pageDataUsers',
        component: pageDataUsersVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/data-karyawan-hak-akses/:randomString',
        name: 'pageKaryawanHakAkses',
        component: pageKaryawanHakAksesVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },

      // PENCATATAN KEHADIRAN
      {
        path: '/check-in/:randomString',
        name: 'pageCheckIn',
        component: pageCheckInVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/check-out/:randomString',
        name: 'pageCheckOut',
        component: pageCheckOutVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/pemulihan/:randomString',
        name: 'pagePemulihan',
        component: pagePemulihanVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/data-absensi/:randomString',
        name: 'pageDataAbsensi',
        component: pageDataAbsensiVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/data-produksi/:randomString',
        name: 'pageDataProduksi',
        component: pageDataProduksiVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },

      // LAPORAN
      {
        path: '/data-laporan-absensi/:randomString',
        name: 'pageLapAbsensi',
        component: pageLapAbsensiVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/data-laporan-gaji/:randomString',
        name: 'pageLapGaji',
        component: pageLapGajiVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },

      // PENGATURAN
      {
        path: '/pengaturan/:randomString',
        name: 'pagePengaturanView',
        component: pagePengaturanViewVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/profil-me/:randomString',
        name: 'pageProfil',
        component: pageProfilVue,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },

      // KOMPONEN
      {
        path: '/lokasi-tambah/:randomString',
        name: 'KomponenTambahLokasi',
        component: KomponenTambahLokasi,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
      {
        path: '/lokasi-edit/:randomString',
        name: 'KomponenEditLokasi',
        component: KomponenEditLokasi,
        meta: {
          middleware: [permissionMiddleware], // Tambahkan middleware ke meta
        },
      },
    ],
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  mode: 'history',
});

export default router;
