export default {
  data() {
    return {
      defaultColorScrollbar: 'default-color-scroll',
      changeColorScrollbar: '',
      scrollTimer: null,
    };
  },
  methods: {
    style_th() {
      return 'background-color: #292e8c';
    },
    class_th() {
      return 'text-center align-middle text-white fw-bold';
    },
    class_td() {
      return 'align-middle text-center';
    },
    style_td(index, hadirStatus) {
      if (hadirStatus === 'Tidak Hadir') {
        return {
          backgroundColor: 'rgb(255, 198, 198)',
          color: 'red',
        };
      } else if (hadirStatus === 'Izin') {
        return {
          backgroundColor: 'rgb(167, 255, 176)',
          color: 'green',
        };
      } else {
        if (index % 2 == 0) {
          return 'background-color:#d0d2ff';
        } else {
          return 'background-color:#eff1ff';
        }
      }
    },
    getStyle(hadirStatus) {
      if (hadirStatus === 'Tidak Hadir') {
        return {
          backgroundColor: 'rgb(255, 198, 198)',
          color: 'red',
        };
      } else if (hadirStatus === 'Izin') {
        return {
          backgroundColor: 'rgb(167, 255, 176)',
          color: 'green',
        };
      } else {
        return {};
      }
    },
    getStatus(item, date) {
      const attendance = item.find((a) => a.tanggal.split('T')[0] === date);
      if (attendance && attendance.hadir === 'Hadir') {
        return '\u2714';
      } else if (attendance && attendance.hadir === 'Tidak Hadir') {
        return 'a';
      } else if (attendance && attendance.hadir === 'Izin') {
        return 'i';
      } else {
        return '';
      }
    },

    getBackgroundColor(item, date, value) {
      const attendance = item.find((a) => a.tanggal.split('T')[0] === date);
      if (attendance && attendance.hadir === 'Hadir') {
        return '#90ff85';
      } else if (attendance && attendance.hadir === 'Tidak Hadir') {
        return '#ff8787';
      } else if (attendance && attendance.hadir === 'Izin') {
        return '#ff8787';
      } else if (value % 2 == 0) {
        return '#d0d2ff';
      } else {
        return '#eff1ff';
      }
    },
    handleMouseMove() {
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      this.changeColorScrollbar = 'change-color-scrollbar';
      this.scrollTimer = setTimeout(() => {
        this.changeColorScrollbar = [''];
      }, 2000);
    },
  },
  mounted() {
    this.changeColorScrollbar = this.defaultColorScrollbar;
  },
};
